	module.exports = {"data":{"markdownRemark":{"html":"<p>众所周知的原因，每年的5月35号左右，Google 的全线服务会被无情的封锁一段时间，对于程序员来说，少了 Google 就等于要花费更多的时间和精力和寻找资料和文档，所以如何正确上网很重要。<!--more--></p>\n<p>首先你需要一台 vps，推荐最近新起的 vps 服务提供商 <a href=\"https://www.digitalocean.com/?refcode=08226ee9093c\">DigitalOcean</a>，这家服务商的 vps 物美价廉，最低每个月才 $5，也就是差不多 rmb30，而且还它标配的是 SSD 硬盘，除了能作为代理外，在上面跑一些自己的小网站还是没问题的.</p>\n<p>购买 vps 后，在上面安装 nodejs， 如果你装的系统是 ubuntu 或是 debian，直接运行 apt-get install nodejs 安装 nodejs，当然你也可以下载 nodejs 安装包，自己手动编译安装。</p>\n<p>接下来安装 shadowsocks-nodejs，直接运行 npm install -g shadowsocks，在任意的一个目录下创建 config.json，内容如下</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token punctuation\">{</span>\n    <span class=\"token string\">\"server\"</span><span class=\"token punctuation\">:</span><span class=\"token string\">\"0.0.0.0\"</span>，\n    <span class=\"token string\">\"server_port\"</span><span class=\"token punctuation\">:</span><span class=\"token number\">8388</span>，\n    <span class=\"token string\">\"local_port\"</span><span class=\"token punctuation\">:</span><span class=\"token number\">1080</span>，\n    <span class=\"token string\">\"password\"</span><span class=\"token punctuation\">:</span><span class=\"token string\">\"password\"</span>，\n    <span class=\"token string\">\"timeout\"</span><span class=\"token punctuation\">:</span><span class=\"token number\">600</span>，\n    <span class=\"token string\">\"method\"</span><span class=\"token punctuation\">:</span><span class=\"token string\">\"aes-256-cfb\"</span>，\n    <span class=\"token string\">\"local_address\"</span><span class=\"token punctuation\">:</span><span class=\"token string\">\"127.0.0.1\"</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>在当前目录下运行 nohup ssserver > log &#x26; 使其后台自动运行， 当然你也能配置 <a href=\"https://github.com/clowwindy/shadowsocks-nodejs/wiki/Configure-Shadowsocks-nodejs-with-Supervisor\">Supervisor</a>，让其后台自动运行</p>\n<p>然后回到本地(自己的电脑)，同样安装 shadowsocks-nodejs，也是在任意的一个目录下创建 config.json，内容如下</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-javascript\"><code><span class=\"token punctuation\">{</span>\n    <span class=\"token string\">\"server\"</span><span class=\"token punctuation\">:</span><span class=\"token string\">\"0.0.0.0\"</span>， <span class=\"token comment\">//这里填写你 vps 的ip地址</span>\n    <span class=\"token string\">\"server_port\"</span><span class=\"token punctuation\">:</span><span class=\"token number\">8388</span>，\n    <span class=\"token string\">\"local_port\"</span><span class=\"token punctuation\">:</span><span class=\"token number\">1080</span>， <span class=\"token comment\">//这里填写本地的代理端口，如端口被占用的话，可随意更改</span>\n    <span class=\"token string\">\"password\"</span><span class=\"token punctuation\">:</span><span class=\"token string\">\"password\"</span>，\n    <span class=\"token string\">\"timeout\"</span><span class=\"token punctuation\">:</span><span class=\"token number\">600</span>，\n    <span class=\"token string\">\"method\"</span><span class=\"token punctuation\">:</span><span class=\"token string\">\"aes-256-cfb\"</span>，\n    <span class=\"token string\">\"local_address\"</span><span class=\"token punctuation\">:</span><span class=\"token string\">\"127.0.0.1\"</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>接下来在当前目录运行 sslocal。</p>","excerpt":"众所周知的原因，每年的5月35号左右，Google 的全线服务会被无情的封锁一段时间，对于程序员来说，少了 Google 就等于要花费更多的时间和精力和寻找资料和文档，所以如何正确上网很重要。 首先你需要一台 vps，推荐最近新起的 vps…","fields":{"slug":"/e5dab28c3f","tags":[{"name":"proxy","slug":"431387eb72"},{"name":"shadowsocks","slug":"d210b06881"}]},"frontmatter":{"date":"2014-06-12","path":"/usage-of-shadowsocks","title":"使用 shadowsocks 正确上网"}},"site":{"siteMetadata":{"disqusShortName":"devharry","siteUrl":"https://goharry.io"}}},"pathContext":{"slug":"/e5dab28c3f","prev":{"excerpt":"来深圳后体重不断的增加，从当初60公斤左右到现在已经是超重了，脂肪也在身上越来越多，前段时间体检出血压偏高和中度的脂肪肝，所以减肥是不可避免了。为了更科学的计量每天的运动量，就入手了这款智能手环 fibit flex 外观 fitbit…","fields":{"slug":"/7457b2b5fb","tags":[{"name":"fitbit","slug":"ca82ed41cd"}]},"frontmatter":{"path":"/review-of-fitbit-flex","title":"fitbit flex 使用体验","date":"2014-07-19T00:00:00.000Z"}},"next":{"excerpt":"工作中少不了跟 ajax 打交道，本着少造轮子的思想，一般都是使用 jquery 的 ajax 接口，同域的 ajax 大家都知道是通过 xhr(XMLHttpRequest) 请求完成， 在jquery 中，jsonp…","fields":{"slug":"/8d76e7d06e","tags":[{"name":"javascript","slug":"de9b9ed78d"},{"name":"jsonp","slug":"8a8604b628"},{"name":"ajax","slug":"2705a83a5a"}]},"frontmatter":{"path":"/principle-of-jsonp","title":"jsonp 原理","date":"2014-05-13T00:00:00.000Z"}}}}

